import React, { useEffect, useState } from 'react';

import styles from './clients-page.module.css';
import { Link, Navigate } from 'react-router-dom';

import { useIfAdmin } from '../../hooks/use-auth';
import { useDispatch, useSelector } from 'react-redux';
import { setCliensData } from '../../store/slices/clientsSlice';

import {
  getFirestore,
  collection,
  query,
  where,
  getDocs,
  deleteDoc,
  doc,
  getDoc,
  orderBy,
  updateDoc,
  or,
  and,
} from 'firebase/firestore';

import { app } from '../../firebase';
import LoaderElementSticky from '../../components/LoaderElementSticky/LoaderElementSticky';
import LoaderPage from '../../components/LoaderPage/LoaderPage';
import RestartBtn from '../../components/RestartBtn/RestartBtn';
import CompareBtn from '../../components/СompareBtn/СompareBtn';
const db = getFirestore(app);

const getClientsHtml = (clients) => {
  return clients.map((item) => {
    return (
      <div
        key={item.time_milliseconds}
        className={
          item.isAdmin ? styles.client_wrapper + ' ' + styles.admin : styles.client_wrapper
        }>
        <Link to={'/smile/admin-client/' + item.g_localId} className={styles.client_link}>
          <span>{item.name ? item.name : "Ім'я не вказано"}</span>
          {item.isAdmin ? <span>Admin</span> : ''}
          <div className={styles.client_info_wrapper}>
            {item.groupAccompany ? (
              <div className={styles.info_item + ' ' + styles.info_item_groupAccompany}>Г</div>
            ) : (
              ''
            )}
            {item.individualAccompany ? (
              <div className={styles.info_item + ' ' + styles.info_item_individual}>І</div>
            ) : (
              ''
            )}
            {item.calculated ? (
              <div className={styles.info_item + ' ' + styles.info_item_calculated}>₴</div>
            ) : (
              ''
            )}
          </div>
        </Link>

        <CompareBtn user={item} />
      </div>
    );
  });
};

const ClientsPage = () => {
  // console.log('render');

  const dispatch = useDispatch();
  const [restart, setRestart] = useState(null);
  const isAdmin = useSelector((state) => state.user.isAdmin);
  const { clients, sortValue } = useSelector((state) => state.clients);

  const [loader, setLoader] = useState(false);

  useEffect(() => {
    // Отримує дані з firestore (з бекенда)
    const getClients = async () => {
      // if (!clients.length || restart === 'restart') {
      setLoader(true);

      const getFilter = (sortValue) => {
        if (sortValue === 'name' || sortValue === 'deteNew' || sortValue === 'deteOld') {
          return '';
        } else if (sortValue === 'groupAccompanyIndividualAccompany') {
          return or(where('groupAccompany', '==', true), where('individualAccompany', '==', true));
        } else if (sortValue === 'noCalculated') {
          return and(
            or(where('groupAccompany', '==', true), where('individualAccompany', '==', true)),
            where('calculated', '==', false),
          );
        } else if (sortValue === 'noAccompany') {
          return and(
            where('groupAccompany', '==', false),
            where('individualAccompany', '==', false),
          );
        } else if (sortValue === 'deteNew') {
          // return and(where('groupAccompany', '==', false), where('individualAccompany', '==', false));
        } else {
          return where(sortValue, '==', true);
        }
      };

      const getSort = (sort) => {
        if (sort === 'newClient') {
          return orderBy('time_milliseconds', 'desc');
        } else if (sort === 'oldClient') {
          return orderBy('time_milliseconds');
        } else {
          return orderBy('name');
        }
      };

      const q = query(
        collection(db, 'clients'),
        getFilter(sortValue.filter),
        getSort(sortValue.sort),
      );

      const clientsBec = [];

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // console.log(doc.id, ' => ', doc.data());
        clientsBec.push(doc.data());
      });

      // setTimeout(() => {
      dispatch(setCliensData(clientsBec));
      setLoader(false);
      setRestart(null);
      // }, 5000);
      // }
    };

    window.scrollTo({
      top: localStorage.getItem('scrollY'),
      behavior: 'instant',
    });
    getClients();

    if (restart) {
      return () => {
        // обнуляє скрол в localStorage
        localStorage.setItem('scrollY', 0);
      };
    }
  }, [sortValue, restart]);

  return useIfAdmin() && isAdmin ? (
    <div className="page">
      <div className="container">
        {clients && clients.length ? (
          <div className={styles.clientsPage_wrapper}>
            {getClientsHtml(clients)}
            {loader ? <LoaderElementSticky /> : ''}
            <RestartBtn handleClick={setRestart} clientsLength={clients.length} />
          </div>
        ) : loader ? (
          <LoaderPage />
        ) : (
          <div className={styles.notClients}>Пусто :)</div>
        )}
      </div>
    </div>
  ) : (
    <Navigate to="/smile/login" />
    // <Navigate to="/login-for-tst" />
  );
};

export default ClientsPage;
